import React from "react"

import Layout from "../components/layout"
import SEO from "../components/seo"

import Intro from "../components/intro"
import UsosEs48 from "../components/usos-es48"
import Control from "../components/control"
import Slidder from "../components/slidder"
import Info from "../components/info"
import Contact from "../components/contact"
import axios from 'axios';

export default class SecondPage extends React.Component {
  constructor(props) {
		super(props);
		this.state = {
			nombre : '',
			telefono : '',
			correo : '',
			asunto : '',
			interes : '',
			mensaje : ''
		}
  }
  sendEmail = (e) => {
		e.preventDefault();
    
    const message = ' Nombre: ' + this.state.nombre + ' \n Teléfono: ' +  this.state.telefono + ' \n Correo: ' + this.state.correo + ' \n Asunto: ' + this.state.asunto + ' \n Área de Interes: ' + this.state.interes + ' \n Mensaje: ' + this.state.mensaje;
    
    axios.post('https://www.damsa.com.mx/WebSiteCore/WebApiEnvioDeCorreo/api/Correos', {
    		Id_Sistema: "SITIO_CONTACTO",
  			De: "noreply@damsa.com.mx",
  			Para: "nnino@damsa.com.mx",
  			Copia: "nnino@damsa.com.mx",
  			CopiaOculta: "nnino@damsa.com.mx",
  			Asunto: "Correo enviado desde el formulario de contacto",
  			Msg: message

  		})
  		.then(function (response) {
    		alert("Mensaje enviado correctamente")
  		})
  		.catch(function (error) {
    		console.log(error);
  		});
  }

  handleInputChange = event => {
    const target = event.target
    const value = target.value
    const name = target.name

    this.setState({
      [name]: value,
    })
    console.log(value)
  }

  render() {
  return(
  <Layout>
    <SEO title="SISTEMA ES48 " />
    <Intro title="SISTEMA ES48" slogan="rectificador y controlador multifunción en 48 VDC" />

    <UsosEs48/>
    <Control/>
    <Slidder/>
    <Info/>
    <Contact sendEmail={this.sendEmail} handleInputChange={this.handleInputChange} />
  </Layout>
  ) 
}
}

